export default ()=> {
      return (
        <div className="question" data-index="48" dangerouslySetInnerHTML = {{ __html: `
<h6>48. 输出是什么?</h6>
<pre class="language-javascript"><code class="language-javascript">console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token number">3</span> <span class="token operator">+</span> <span class="token number">4</span> <span class="token operator">+</span> <span class="token string">"5"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>&quot;345&quot;</code></li>
<li>B: <code>&quot;75&quot;</code></li>
<li>C: <code>12</code></li>
<li>D: <code>&quot;12&quot;</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>当所有运算符的 <em>优先级</em> 相同时，计算表达式需要确定运算符的结合顺序，即从右到左还是从左往右。在这个例子中，我们只有一类运算符<code>+</code>，对于加法来说，结合顺序就是从左到右。</p>
<p><code>3 + 4</code>首先计算，得到数字<code>7</code>.</p>
<p>由于类型的强制转换，<code>7 + '5'</code>的结果是<code>&quot;75&quot;</code>. JavaScript将<code>7</code>转换成了字符串，可以参考问题15.我们可以用<code>+</code>号把两个字符串连接起来。 <code>&quot;7&quot; + &quot;5&quot;</code> 就得到了<code>&quot;75&quot;</code>.</p>
</p>
</details>
` }}></div>
      );
    }